<template>
    <div class="abouts-container" :key="about.id" v-for="about in abouts">
        <About @expand-about="$emit('expand-about', about.id)" :about='about'/>
        <AboutExpanded v-if="about.expand && about.selection==='about'" :about='about'/>
        <ContactsExpanded v-if="about.expand && about.selection==='contact'" @hover-contact="hover" @unhover-contact="unhover" :contacts='contacts' />
    </div>
</template>

<script>
import About from './About'
import AboutExpanded from './AboutExpanded'
import ContactsExpanded from './AboutContactsExpanded'

export default {
    name: 'Abouts',
    props: {
        abouts: Array,
        contacts: Array
    },
    components: {
        About,
        AboutExpanded,
        ContactsExpanded
    },
    emits: ['expand-about', 'hover-contact', 'unhover-contact'],
    methods: {
        hover(id){
            this.$emit('hover-contact',id)
        },
        unhover(id){
            this.$emit('unhover-contact',id)
        }
    }
}
</script>

<style scoped>
    .abouts-container {
        cursor: pointer;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        width: 100vw;
    }
</style>