<template>
    <div :key="career.id" v-for="career in careers">
        <Title v-if='career.expand' :career='career' @click="$emit('career-initial', career.id)"/>
    </div>
    <div class="careers-container" :class="[career.initialState ? 'larger-div' : '']" :key="career.id" v-for="career in careers">
        <div :class="[career.showCareer ? 'careers' : '']">
            <!-- Landing Careers View -->
            <Career v-if='career.initialState' @toggle-select-career="$emit('toggle-select-career', career.id)" @hide-gif="$emit('hide-gif', career.id)" @show-gif="$emit('show-gif', career.id)" :career='career'/>

            <!-- Expanded and minimized Careers View -->
            <CareerExpanded v-if='career.expand' :career='career'/>
            <CareerMinimized v-if="career.showCareer && !career.initialState && !career.expand" @career-expand="$emit('career-expand', career.id)" :career='career'/>
        </div>
        <!-- Closed Careers View for expanded Abouts -->
        <CareerClosed v-if="!career.showCareer" @toggle-select-career="$emit('toggle-select-career', career.id)" :career='career'/>
    </div>
    
</template>

<script>
import Title from './Title'
import Career from './Career'
import CareerExpanded from './CareerExpanded'
import CareerMinimized from './CareerMinimized'
import CareerClosed from './CareerClosed'

export default {
    name: 'Careers',
    props: {
        careers: Array,
    },
    components: {
        Title,
        Career,
        CareerExpanded,
        CareerMinimized,
        CareerClosed
    },
    emits: ['toggle-select-career', 'career-expand', 'hide-gif', 'show-gif', 'career-initial']
}
</script>

<style scoped>
    .careers-container {
        display: inline-flex;
    }
    .careers {
        height: 65vh;
    }
    .larger-div {
        height: 78vh;
    }
</style>