<template>
    <div class="contacts-container">
        <div @mouseover="$emit('hover-contact', contact.id)" @mouseleave="$emit('unhover-contact', contact.id)" @click="CopyUrl(contact.link)" :key="contact.id" v-for="contact in contacts">
            <div :class="{'hovered':contact.hover===true}">
                <img :class="{'smaller-img':contact.title==='Kakao'}" :src="require(`@/assets/contacts/${contact.image}`)" :alt='contact.title'>
                <h3><i :class="contact.logo"></i>{{contact.title}}</h3>
                <div class="contact-link" ref="url" v-if="contact.hover===true">{{contact.link}}</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ContactsExpanded',
    props: {
        contacts: Array,
    },
    emits: ['hover-contact', 'unhover-contact'],
    methods: {
        CopyUrl(url){
            navigator.clipboard.writeText(url)
            alert("'"+url+"'" + " has been copied to your clipboard!")
        }
    }
}
</script>

