<template>
  <div class="main-container">
    <Careers @toggle-select-career='ToggleSelectCareer'  @career-expand='ToggleSelectCareer' @hide-gif='HideGif' @show-gif='ShowGif'  @career-initial='CareerInitial' :careers='careers'/>
    <Abouts @expand-about='AboutExpand' @hover-contact="HoverContact" @unhover-contact="UnhoverContact" :abouts='abouts' :contacts='contacts'/>
  </div>
</template>

<script>
import Careers from './components/Careers'
import Abouts from './components/Abouts'

export default {
  name: 'App',
  components: {
    Careers,
    Abouts,
  },
  data () {
    return {
      careers: [],
      abouts: [],
      contacts: []
    }
  },
  methods: {
    async fetchData(group, id='') {
      const res = await fetch(`api/`+group+`/${id}`)
      const data = await res.json()
      return data
    },
    CareerInitial(){
      this.careers.forEach(career => {career.initialState=true, career.expand=false, career.showCareer=true})
    },
    ToggleSelectCareer(id) {
      this.careers.forEach(career => {career.initialState=false, career.expand=false, career.showCareer=true})
      this.abouts.forEach(about => {about.expand=false})
      this.careers = this.careers.map((career) => career.id === id ? {...career, expand: !career.expand} : career)
    },
    ShowGif(id) {
      this.careers.forEach(career => {career.gif=false})
      this.careers = this.careers.map((career) => career.id === id ? {...career, gif: !career.gif} : career)
    },
    HideGif() {
      this.careers.forEach(career => {career.gif=false})
    },
    AboutExpand(id) {
      this.careers.forEach(career => {career.initialState=false, career.expand=false, career.showCareer=false})
      this.abouts.forEach(about => {about.expand=false})
      this.abouts = this.abouts.map((about) => about.id === id ? {...about, expand: !about.expand} : about)
    },
    HoverContact(id) {
      this.contacts.forEach(contact => {contact.hover=false})
      this.contacts = this.contacts.map((contact) => contact.id === id ? {...contact, hover: !contact.hover} : contact)
    },
    UnhoverContact(id) {
      this.contacts = this.contacts.map((contact) => contact.id === id ? {...contact, hover: !contact.hover} : contact)
    },
  },
  async created() {
    // EDIT THE BELOW OBJECTS MANUALLY
    this.careers = await this.fetchData('careers'),
    this.abouts = await this.fetchData('abouts'),
    this.contacts = await this.fetchData('contacts')
    // https://www.qrcode-monkey.com/ for QR CODEs
  }
}
</script>

